<template>
    <div class="top-up">
        <div class="top-c">
            <div class="asset-title">
                <span class="desc"></span>
                <span class="name">{{pname}}{{$t('center.topUp[0]')}}</span>
            </div>
            <template>
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <!-- 充值地址 -->
                    <el-tab-pane :label="$t('center.topUp[1]')" name="first">
                        <el-input disabled v-model="url"></el-input>
                        <el-button class="up-btn" @click="copyCode">{{$t('center.topUp[2]')}}</el-button>
                        <p class="view" @click="goCapitalSubsidiary">{{$t('center.topUp[3]')}}</p>
                        <p class="title">{{$t('center.topUp[4]')}}</p>
                        <p class="tips">{{$t('center.topUp[5]')}}</p>
                        <p class="tips">{{$t('center.topUp[6]')}}</p>
                        <p class="tips">{{$t('center.topUp[7]')}}</p>
                        <p class="tips">{{$t('center.topUp[8]')}}</p>
                    </el-tab-pane>
                    <!-- 二维码 -->
                    <el-tab-pane :label="$t('center.topUp[9]')" name="second">
                        <img style="width:185px;height:auto;" :src="qrc">
                    </el-tab-pane>
                </el-tabs>
            </template>
        </div>
    </div>
</template>

<script>
import {bpay} from "@/api/user.js"
export default {
    name: 'topUp',
    data() {
        return {
            url:'',
            qrc:'',
            activeName: 'first',
            pname:this.$route.params.pname
        }
    },
    created () {
        bpay({pid:this.$route.params.id}).then(res => {
            if(res.data.status == 200){
                this.url = res.data.data.url
                this.qrc = this.$public.baseUrl+'/'+res.data.data.qrc
            } else{
                this.$public.msg(res.data.msg,'error',this)
            }
        })
    },
    methods: {
        // 跳转到资金明细
        goCapitalSubsidiary(){
            this.$router.push({
                path: "/center/capitalSubsidiary",
            })
        },
        handleClick(tab, event) {
            console.log(tab, event);
        },
        copyCode(){
            this.copyText(this.url)
        },
        copyText(text, callback){
            let tag = document.createElement('input');
            tag.setAttribute('id', 'cp_hgz_input2');
            tag.value = text;
            document.getElementsByTagName('body')[0].appendChild(tag);
            document.getElementById('cp_hgz_input2').select();
            document.execCommand('copy');
            document.getElementById('cp_hgz_input2').remove();
            if(callback) {callback(text)}
        },
    }
}
</script>

<style lang=less>
  @import '../../../assets/index.less';
  @import './topUp.less';
</style>


